.top-container {
  position: relative;
  z-index: 2;
  background-color: $white;
}
.header {
  max-width: 1225px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 2;
  padding: 20px 10px;

  @media (min-width:$largeScreens) {
    padding: 0;
  }

  
  
  .logo {
    max-width: 100px;
    max-height: 20px;
  }

  nav {
    text-align: center;
      ul {
        display: none;

        @media (min-width:$largeScreens) {
          font-size: 12px;
          list-style: none;
          margin: 0;
          padding: 0;
          display: flex;
        }

        li {
          margin: 0 5px;
        
          a {
            color: $grayishBlue;
            width: 55px;
            padding: 30px 0;
            display: block;
            transition: color .5s ease-in-out;
            text-decoration: none;
    
            &:hover {
              color: $darkblue;
              }
          }

          .border {        
            content: "";
            height: 4px;
            display: block;
            background: $white;
          }

          a:hover + .border {
           background: linear-gradient(90deg, $limeGreen 0%, $brightCyan 100%);
          }
          
        }

       
      }
  }

  &__menu {
    position: relative;
    .burger {
      position: absolute;
      width:20px;
      height: 15px;
      top: 0;
      right: 10px;
      transition: opacity .3s;

      @media (min-width:$largeScreens) {
        display: none;
      }
    
    }

    .hide-burger {
      display: none;
    
    }

    .close-burger {
      opacity: 0;
      transition: opacity .3s;
     
    }


  }

  a.btn {
    display: none;

        @media (min-width:$largeScreens) {
          display: block;
        }
  }
}